:root {
  --font-primary: "Oswald", "Arial", sans-serif;
  --font-secondary: "Lato", "Arial", sans-serif;
  --color-font-basic: #444444;
  --color-font-basic-active: rgba(68, 68, 68, 0.3);
  --color-font-second: #000000;
  --color-font-second-hover: rgba(0, 0, 0, 0.6);
  --color-font-second-active: rgba(0, 0, 0, 0.3);
  --color-font-third: #111111;
  --color-font-third-hover: #3e3e3e;
  --color-font-third-active: #6b6b6b;
  --color-font-card: #222222;
  --color-font-bg: #ffffff;
  --color-font-bg-hover: rgba(255, 255, 255, 0.6);
  --color-font-bg-active: rgba(255, 255, 255, 0.3);
  --color-font-counter: #f2f2f2;
  --color-bg-basic: #ffffff;
  --color-bg-second: #f2f2f2;
  --color-bg-image: #68b738;
  --color-bg-example: #eaeaea;
  --color-btn-basic: #68b738;
  --color-btn-basic-hover: #5eaa2f;
  --color-btn-basic-active: rgba(94, 170, 47, 0.6);
  --color-btn-more: #f2f2f2;
  --color-btn-more-hover: #ebebeb;
  --color-bd-basic: #d9d9d9;
  --color-bd-second: #cdcdcd;
  --color-bd-third: #e6e6e6;
  --color-bd-input: #e7e7e7;
  --color-bd-card: #ebebeb;
  --color-bd-card-active: rgba(235, 235, 235, 0.3);
  --color-bd-link: #68b738;
  --color-bd-bg: #ffffff;
  --color-bd-input-focus: #444444;
  --color-bd-input-error: #ff8282;
  --color-bd-radio-hover: #bababa;
  --color-bd-radio-hover-opacity: rgba(186, 186, 186, 0.3);
  --color-icon-basic: #000000;
  --color-icon-second: #666666;
  --color-icon-hover: #68b738;
  --color-icon-error: #ff8282;
  --color-icon-plus: #d3d3d3;
  --color-radio-marker: #68b738;
  --padding-page: calc(20px + 0.0223214286 * (100vw - 320px));
}
@media (width >= 768px) {
  :root {
    --padding-page: calc(30px + 0.119047619 * (100vw - 768px));
  }
}
@media (width >= 1440px) {
  :root {
    --padding-page: 110px;
  }
}
@media (width >= 1900px) {
  :root {
    --padding-page: calc(50vw - 840px);
  }
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.page {
  height: 100%;
  font-family: var(--font-primary);
  font-size: 16px;
  line-height: 18px;
  color: var(--color-font-basic);
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-bg-basic);
}

.page__header {
  margin-bottom: 27px;
}
@media (width >= 768px) {
  .page__header {
    margin-bottom: 61px;
  }
}
@media (width >= 1440px) {
  .page__header {
    margin-bottom: 69px;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
}

.header {
  position: relative;
  display: block;
}
@media (width >= 768px) {
  .header {
    padding-inline: var(--padding-page);
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: flex-start;
    padding-top: 25px;
  }
}
@media (width >= 1440px) {
  .header {
    padding: 0;
  }
}
@media (width < 768px) {
  .header--js-menu_closed, .header--js-menu_opened {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--color-bd-basic);
  }
  .header--js-menu_closed.header--index, .header--js-menu_opened.header--index {
    border-bottom: none;
  }
}
.header--index {
  margin: 0;
}
@media (width >= 1440px) {
  .header--index {
    grid-template-columns: 50% 50%;
    margin-bottom: 82px;
  }
  .header--index::before {
    content: "";
    z-index: -1;
    display: block;
    grid-area: 1/2/3/3;
    width: 100%;
    height: 100%;
    background-color: #62a53a;
    background-position: left center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .no-webp .header--index::before {
    background-image: url("../img/background-promo-desktop.jpg");
  }
  .webp .header--index::before {
    background-image: url("../img/background-promo-desktop.webp");
  }
}
@media (width >= 1440px) and (min-resolution: 2dppx) {
  .no-webp .header--index::before {
    background-image: url("../img/background-promo-desktop@2x.jpg");
  }
  .webp .header--index::before {
    background-image: url("../img/background-promo-desktop@2x.webp");
  }
}

.header__logo {
  display: inline-flex;
  align-items: center;
  padding: 13px 20px 14px;
}
@media (width >= 768px) {
  .header__logo {
    row-gap: 13px;
    column-gap: 13px;
    justify-content: start;
    width: max-content;
    margin-right: 32px;
    padding: 0;
  }
}
@media (width >= 1440px) {
  .header__logo {
    grid-area: 1/1/2/2;
    row-gap: 14px;
    column-gap: 14px;
    margin: 0;
    margin-top: 55px;
    margin-left: var(--padding-page);
  }
}

.header__wordmark {
  position: absolute;
  right: var(--padding-page);
  height: fit-content;
  padding-block: 23px;
}
@media (width >= 768px) {
  .header__wordmark {
    position: static;
    right: auto;
    padding: 0;
  }
}
@media (width < 768px) {
  .header--js-menu_closed .header__wordmark, .header--js-menu_opened .header__wordmark {
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__toggle {
  display: none;
  width: 44px;
  height: 44px;
  padding: 0;
  background-color: var(--color-bg-basic);
  border: none;
  cursor: pointer;
}
@media (width < 768px) {
  .header--js-menu_closed .header__toggle {
    display: inline-flex;
    margin-right: 11px;
    padding: 10px;
  }
  .header--js-menu_closed .header__toggle::before {
    content: "";
    width: 24px;
    height: 2px;
    margin-top: 1px;
    background-color: var(--color-icon-basic);
    box-shadow: 0 8px 0 0 var(--color-icon-basic), 0 16px 0 0 var(--color-icon-basic);
  }
  .header--js-menu_opened .header__toggle {
    display: inline-grid;
    margin-right: 7px;
  }
  .header--js-menu_opened .header__toggle::before, .header--js-menu_opened .header__toggle::after {
    content: "";
    grid-area: 1/1/2/2;
    width: 25px;
    height: 2px;
    margin: auto;
    background-color: var(--color-icon-basic);
  }
  .header--js-menu_opened .header__toggle::before {
    transform: rotate(45deg);
  }
  .header--js-menu_opened .header__toggle::after {
    transform: rotate(-45deg);
  }
}
@media (hover: hover) {
  .header__toggle:hover::before, .header__toggle:hover::after, .header__toggle:focus-visible::before, .header__toggle:focus-visible::after {
    opacity: 0.6;
  }
}
.header__toggle:active::before, .header__toggle:active::after {
  opacity: 0.3;
}

@media (width >= 768px) {
  .header__nav {
    justify-self: end;
    margin-right: 3px;
  }
}
@media (width >= 1440px) {
  .header__nav {
    grid-area: 1/2/2/3;
    margin-right: 0;
    padding-inline: 33px var(--padding-page);
    padding-top: 55px;
  }
}
@media (width < 768px) {
  .header--js-menu_closed .header__nav {
    display: none;
  }
  .header--js-menu_opened .header__nav {
    width: 100%;
  }
}
@media (768px <= width < 1440px) {
  .header--index .header__nav {
    margin-bottom: 61px;
  }
}

@media (width < 768px) {
  .header--js-menu_opened .header__list {
    position: absolute;
    right: 0;
    left: 0;
    z-index: 1;
  }
}

.header__promo {
  width: 100%;
}
@media (width >= 768px) {
  .header__promo {
    grid-area: 2/1/3/3;
  }
}

.container {
  padding-inline: var(--padding-page);
}

.logo {
  text-decoration: none;
}
@media (hover: hover) {
  .logo:hover .logo__icon, .logo:hover .logo__wordmark, .logo:focus-visible .logo__icon, .logo:focus-visible .logo__wordmark {
    opacity: 0.8;
  }
}
.logo:active .logo__icon, .logo:active .logo__wordmark {
  opacity: 0.6;
}

.logo__icon {
  display: block;
}
@media (width >= 768px) {
  .logo__icon {
    width: 60px;
    height: 50px;
  }
}
@media (width >= 1440px) {
  .logo__icon {
    width: 70px;
    height: 59px;
  }
}

.logo__wordmark {
  --cat-color: var(--color-font-third);
  --energy-color: var(--color-font-third);
  display: block;
}
@media (width >= 768px) {
  .logo__wordmark {
    --energy-color: var(--color-font-basic);
  }
}
@media (width >= 1440px) {
  .logo__wordmark {
    width: 118px;
    height: 21px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: var(--color-bg-basic);
  border-top: 1px solid var(--color-bd-third);
}
@media (width >= 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 8px;
    column-gap: 33px;
    border-top: none;
  }
}
@media (width >= 1440px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 33px;
    background-color: transparent;
  }
}

.nav__item {
  text-align: center;
  border-bottom: 1px solid var(--color-bd-third);
}
@media (width >= 768px) {
  .nav__item {
    border-bottom: none;
  }
}

.nav__link {
  display: block;
  width: fit-content;
  margin: 0 auto;
  padding: 22px 20px 23px;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-font-second);
}
@media (width >= 768px) {
  .nav__link {
    padding: 15px 0;
    font-size: 18px;
    line-height: 24px;
    text-align: start;
  }
  .nav__link--current::after {
    content: "";
    display: block;
    width: calc(100% - 2px);
    margin: 0 auto;
    box-shadow: 0 6px 0 1px var(--color-bd-link);
  }
  .header--index .nav__link--current::after {
    box-shadow: 0 8px 0 1px var(--color-bd-link);
  }
}
@media (width >= 1440px) {
  .nav__link {
    padding-block: 17px;
    font-size: 20px;
    line-height: 30px;
  }
  .header--index .nav__link {
    color: var(--color-font-bg);
  }
}
@media (width >= 1440px) and (hover: hover) {
  .header--index .nav__link:not(span):hover, .header--index .nav__link:not(span):focus-visible {
    color: var(--color-font-bg-hover);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link:not(span):active {
    color: var(--color-font-bg-active);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link--current::after {
    box-shadow: 0 6px 0 1px var(--color-bd-bg);
  }
}
@media (hover: hover) {
  .nav__link:not(span):hover, .nav__link:not(span):focus-visible {
    color: var(--color-font-second-hover);
  }
}
.nav__link:not(span):active {
  color: var(--color-font-second-active);
}

.promo {
  display: grid;
  place-items: center;
  padding-bottom: 20px;
  text-align: center;
}
@media (width >= 768px) {
  .promo {
    place-items: start;
    padding: 0;
    text-align: start;
  }
}
@media (width >= 1440px) {
  .promo {
    grid-template-rows: min-content min-content 1fr;
    grid-template-columns: 1fr 1fr;
    padding: 24px var(--padding-page) 52px;
  }
}
.promo::before {
  content: "";
  display: block;
  grid-area: 1/1/3/2;
  width: 100%;
  height: 100%;
  min-height: 302px;
  background-position: top;
  background-size: auto 100%;
  background-repeat: no-repeat;
}
.no-webp .promo::before {
  background-image: url("../img/background-promo-phone.jpg"), linear-gradient(to right, #64a73c calc(50vw - 30px), #62a53a calc(50vw + 30px));
}
.webp .promo::before {
  background-image: url("../img/background-promo-phone.webp"), linear-gradient(to right, #64a73c calc(50vw - 30px), #62a53a calc(50vw + 30px));
}
@media (min-resolution: 2dppx) {
  .no-webp .promo::before {
    background-image: url("../img/background-promo-phone@2x.jpg"), linear-gradient(to right, #64a73c calc(50vw - 30px), #62a53a calc(50vw + 30px));
  }
  .webp .promo::before {
    background-image: url("../img/background-promo-phone@2x.webp"), linear-gradient(to right, #64a73c calc(50vw - 30px), #62a53a calc(50vw + 30px));
  }
}
@media (width >= 768px) {
  .promo::before {
    display: none;
  }
}

.promo__title {
  padding-inline: var(--padding-page);
  grid-area: 1/1/2/2;
  margin: 0;
  margin-bottom: 25px;
  padding-top: 27px;
  color: var(--color-font-bg);
}
@media (width >= 768px) {
  .promo__title {
    margin-bottom: 40px;
    padding: 0;
    padding-inline: 90px;
    color: var(--color-font-second);
  }
}
@media (width >= 1440px) {
  .promo__title {
    grid-column: 1/2;
    margin-inline: 80px;
    margin-top: 81px;
    padding: 0;
  }
}

.promo__slogan {
  padding-inline: var(--padding-page);
  grid-area: 2/1/3/2;
  margin: 0;
  padding-bottom: 164px;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: var(--color-font-bg);
}
@media (width >= 768px) {
  .promo__slogan {
    margin-bottom: 21px;
    padding: 0;
    padding-inline: 90px;
    font-size: 20px;
    line-height: 20px;
    color: var(--color-font-second);
  }
}
@media (width >= 1440px) {
  .promo__slogan {
    grid-column: 1/2;
    margin-inline: 80px;
    margin-bottom: 52px;
    padding: 0;
  }
}

.promo__image-container {
  margin-block: -135px 3px;
}
@media (width >= 768px) {
  .promo__image-container {
    grid-area: 3/1/4/2;
    justify-self: center;
    margin: 0;
  }
}
@media (width >= 1440px) {
  .promo__image-container {
    z-index: -1;
    grid-area: 1/2/4/3;
    align-self: center;
    justify-self: start;
    margin-left: -163px;
  }
}

.promo__image {
  display: block;
  width: 280px;
  height: 270px;
}
@media (width >= 768px) {
  .promo__image {
    width: 709px;
    height: 609px;
  }
}
@media (width >= 1440px) {
  .promo__image {
    width: 552px;
    height: 499px;
  }
}

.promo__link {
  width: calc(100% - 2 * var(--padding-page));
}
@media (width >= 768px) {
  .promo__link {
    grid-area: 3/1/4/2;
    width: auto;
    min-width: 244px;
    max-width: 264px;
    margin: 29px 0 0 90px;
  }
}
@media (width >= 1440px) {
  .promo__link {
    grid-column: 1/2;
    max-width: 40%;
    margin: 0;
    margin-inline: 80px;
  }
}

.heading {
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
}
@media (width >= 768px) {
  .heading {
    font-size: 60px;
    line-height: 60px;
  }
}
.heading--subhead {
  position: relative;
  padding-inline: var(--padding-page) 20px;
  font-size: 24px;
  line-height: 26px;
  background-color: var(--color-bg-basic);
}
@media (width >= 768px) {
  .heading--subhead {
    padding-inline: 40px;
    font-size: 36px;
    line-height: 36px;
  }
}
.heading--subhead::before {
  content: "";
  position: absolute;
  left: -100vw;
  z-index: -1;
  width: 200vw;
  box-shadow: 0 16px 0 1px var(--color-bd-link);
}
@media (width >= 768px) {
  .heading--subhead::before {
    box-shadow: 0 23px 0 1px var(--color-bd-link);
  }
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-family: var(--font-primary);
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-font-bg);
  background-color: var(--color-btn-basic);
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .button:hover, .button:focus-visible {
    background-color: var(--color-btn-basic-hover);
  }
}
.button:active {
  color: var(--color-font-bg-active);
  background-color: var(--color-btn-basic-hover);
}
.button:disabled {
  color: var(--color-font-bg);
  background-color: var(--color-btn-basic);
  opacity: 0.3;
  cursor: initial;
}
@media (width >= 768px) {
  .button--promo {
    padding: 14px 26px;
    font-size: 20px;
    line-height: 26px;
  }
}
@media (width >= 768px) {
  .button--form {
    padding: 14px 26px;
    font-size: 20px;
    line-height: 26px;
  }
}
.button--more {
  color: var(--color-font-basic);
  background-color: var(--color-btn-more);
}
@media (hover: hover) {
  .button--more:hover, .button--more:focus-visible {
    background-color: var(--color-btn-more-hover);
  }
}
.button--more:active {
  color: var(--color-font-basic-active);
  background-color: var(--color-btn-more-hover);
}

.main-index__product-series {
  margin-bottom: 20px;
}
@media (width >= 768px) {
  .main-index__product-series {
    margin-bottom: 45px;
  }
}
@media (width >= 1440px) {
  .main-index__product-series {
    margin-bottom: 71px;
  }
}

.main-index__details {
  margin-bottom: 49px;
}
@media (width >= 768px) {
  .main-index__details {
    margin-bottom: 46px;
  }
}
@media (width >= 1440px) {
  .main-index__details {
    margin-bottom: 69px;
  }
}

.product-series__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
@media (width >= 768px) {
  .product-series__list {
    row-gap: 30px;
  }
}
@media (width >= 1440px) {
  .product-series__list {
    flex-direction: row;
    flex-wrap: wrap;
    row-gap: 40px;
    column-gap: 80px;
    justify-content: center;
  }
}

.product-series__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 20px;
  align-items: center;
  padding: 24px 20px 18px;
  background-color: var(--color-bg-second);
}
@media (width >= 768px) {
  .product-series__item {
    grid-template-columns: 1fr 200px;
    column-gap: 53px;
    padding: 53px 63px 53px 52px;
  }
}
@media (width >= 1440px) {
  .product-series__item {
    grid-template-rows: min-content 1fr;
    grid-template-columns: min-content 1fr;
    column-gap: 62px;
    width: calc(50% - 40px);
    padding: 47px 52px 55px;
  }
}
.product-series__item::before {
  content: "";
  grid-area: 1/1/2/2;
  width: 50px;
  height: 50px;
  background-color: var(--color-bg-image);
  border-radius: 50%;
}
@media (width >= 768px) {
  .product-series__item::before {
    grid-area: 1/2/4/3;
    width: 200px;
    height: 200px;
    margin-top: 11px;
  }
}
@media (width >= 1440px) {
  .product-series__item::before {
    grid-area: 1/1/2/2;
    width: 100px;
    height: 100px;
    margin-top: 0;
  }
}
.product-series__item::after {
  content: "";
  position: relative;
  grid-area: 1/1/2/2;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (width >= 768px) {
  .product-series__item::after {
    grid-area: 1/2/4/3;
  }
}
@media (width >= 1440px) {
  .product-series__item::after {
    grid-area: 1/1/2/2;
  }
}
.product-series__item--icon_slim::after {
