.academy__icon {
  display: block;
  flex-shrink: 0;
  pointer-events: auto;
  fill: var(--color-icon-second);
}
@media (hover: hover) {
  .academy:hover .academy__icon, .academy:focus-visible .academy__icon {
    fill: var(--color-icon-hover);
  }
}
.academy:active .academy__icon {
  opacity: 0.3;
  fill: var(--color-icon-hover);
}